.container {
    color: white;
    /* height: 100vh; */
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    width: 50%;
    gap: 30px;
}

@media screen and (max-width: 1000px){
    .container {
        width: 60%;
    }
}

@media screen and (max-width: 750px){
    .container {
        width: 70%;
    }
}

@media screen and (max-width: 450px){
    .container {
        width: 90%;
        font-size: 0.7rem;
    }
}