.form-input {
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding: 20px 0px;
  background-image: linear-gradient(
    rgba(74, 105, 99, 0.528),
    rgba(1, 2, 1, 0)
  );
  border-radius: 20px;
}
.input-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
}

.my-fieldset {
  width: 40%;
  border-radius: 0px 9px;
}

legend {
  font-size: 0.65rem;
  padding: 3px 5px;
}

.input-container .my-fieldset input {
  background-color: transparent;
  border: none;
  width: 100%;
  padding: 7px;
  color: orange;
  outline: none;
  font-size: 1.1rem;
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.btn-1 {
  padding: 5px;
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
}

.btn-2 {
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: rgba(114, 164, 155, 0.074);
  color: white;
  border: none;
  box-shadow: 1.5px 1.5px 0px black;
}


