@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200;300&family=Roboto:wght@300&display=swap");

.result {
  width: 100%;
}

.result table {
  width: 100%;
}

th {
  text-align: end;
  font-size: 0.9rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  padding: 10px 13px;
  color: rgb(84, 156, 137);
}

td {
  text-align: end;
  font-size: 0.9rem;
  padding: 5px 13px;
  font-weight: 200;
  font-family: "Roboto", sans-serif;
}

tbody {
  overflow-y: scroll;
}

@media screen and (max-width: 450px) {
  th {
    padding: 10px 5px;
    font-size: 0.8rem;
  }
  td {
    padding: 5px 5px;
    font-size: 0.64rem;
  }
}
